<template>
    <div class="container stories-wrapper">
        <h2 v-if="showTitle"
            class="block__title"
        >
            В подписку входит
        </h2>
        <div class="stories">
            <div v-for="story in newStories"
                 :key="story"
                 class="story"
            >
                <a
                    :href="`isapp://lk?screen=Stories&url=${story.mlUrl}&id=${story.mlId}`"
                    class="story__link"
                >
                    <img :src="story.image"
                         alt="image"
                         class="story__img"
                    />
                </a>
            </div>
        </div>
    </div>
</template>

<script>
    import { defineComponent } from "vue";

    export default defineComponent({
        name: "NewStories",
        props: {
            showTitle: {
                type: Boolean,
                default: true,
            },
        },
        computed: {
            userID() {
                return this.$store.state.userInfo.USER_ID;
            },
            newStories() {
                return this.$store.state.stories;
            },
        },
        watch: {
            userID() {
                this.$store.dispatch("getStories", this.userID);
            },
        },
        mounted() {
            if (!Object.keys(this.newStories).length) {
                this.$store.dispatch("getStories", this.userID);
            }
        },
    });
</script>

<style scoped>
.stories-wrapper {
  margin-top: 24px;
  box-sizing: border-box;
}

.stories {
  width: calc(100% + 32px);
  height: auto;
  min-height: 200px;
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: stretch;
  flex-direction: row;
  box-sizing: border-box;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
  scroll-padding-inline-start: 16px;
}

.stories::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  display: none;
}

.story {
  width: 150px;
  height: 200px;
  display: flex;
  position: relative;
  flex-shrink: 0;
  scroll-snap-align: start;
  border-radius: 22px;
}

.story:not(:last-child) {
  margin-right: 8px;
}

.story:first-of-type {
  padding-left: 16px;
}

.story:last-of-type {
  padding-right: 16px;
}

.story__link {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  color: #ffffff;
  border-radius: 16px;
  text-decoration: none;
  box-sizing: border-box;
}

.story__title {
  margin-bottom: 4px;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: -0.24pt;
}

.story__description {
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: -0.44pt;
}

.story__img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: fill;
  object-position: center;
  inset: 0;
  border-radius: 16px;
}
</style>